:root {
  --color-grey-dark:     hsla(191, 100%, 3%, 1);
  --color-grey-middle:   hsla(197, 66%, 84%, 0.51);
  --color-grey-eeeeee:   hsl(0, 0%, 93%, 1);
  --color-light-bright:  hsla(360, 100%, 100%, 1);
  --color-fuchsia:       hsla(300,100%,50%,1);
  --color-transparent:   transparent;
}

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

h1,h2,h3,p,
button:hover { color: var(--color-light-bright); }


.App {
  text-align: center;
}


.App-header {
  background: var(--color-grey-dark);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);

}



button {
  margin: 0;
  padding: 20px;
  font-size: 20px;
  background-color: #90e7e7;
  margin: 2px; 
  color: var(--color-fuchsia);
  font-weight: 600;
  aspect-ratio: 16/9;
  width: 7rem;
  margin: 0.5rem;
  background-color: var(--color-transparent);
  border-radius: 1.1rem;
  border: 2px solid var(--color-grey-middle);
  opacity: 1;
  font-family: 'Montserrat', sans-serif;
}

section h2 {
  margin: 0;
  background: -webkit-linear-gradient(var(--color-grey-eeeeee), var(--color-grey-dark));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: 'Montserrat', sans-serif;
  font-size: 17rem;
}

footer {
  font-size: 15px;
  opacity: 80%;
}

main h1{
  color: var(--color-grey-middle);
  font-weight: 400;
  font-family: 'Montserrat', sans-serif;
  font-size: 6rem; 
  margin: 0;
}
a { text-decoration: none; }